body {
  position: absolute;
  top: 40%;
  left: 35%;
  overflow: auto;
  background-color: #adff2f;
}

ul {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
li {
  list-style-type: none;
  margin-top: 20px;
  font-size: 38px;
}

a {
  text-decoration: none;
  color: red;
}

a:hover {
  color: blue;
}
